<template>
  <div class="container-fluid p-0">
    <div class="row" id="riwayat">
      <div class="col-md-12">
        <h1 class="h1 mb-0">Riwayat Perwalian</h1>
        <br />
        <div class="card">
          <div class="card-body">
            <div class="table-striped table-responsive">
              <table class="table mb-0 text-center">
                <thead>
                  <tr>
                    <th scope="col">No</th>
                    <th scope="col">Semester</th>
                    <th scope="col">SKS</th>
                    <th scope="col">Status Perwalian</th>
                    <th scope="col">Info</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(riper, index) in getDataRiper"
                    :key="riper.guardianship_id"
                  >
                    <td>{{ number(index) }}</td>
                    <td>{{ riper.semester }}</td>
                    <td>{{ riper.credits_total }}</td>
                    <td class="text-orange-accent" >
                      <mark class=" badge badge-success badge-lg" >{{
                        riper.guardianship_status
                      }}</mark>
                    </td>

                    <td>
                      <button
                        class="btn btn-success btn-sm"
                        data-toggle="modal"
                        :data-target="'#detail' + number(index)"
                        @click.prevent="getDetailAll(riper.guardianship_id)"
                      >
                        Detail
                      </button>
                      <!-- The Modal -->
                      <div
                        :id="'detail' + number(index)"
                        class="modal fade bd-example-modal-lg"
                        tabindex="-1"
                        role="dialog"
                        aria-labelledby="myLargeModalLabel"
                        aria-hidden="true"
                      >
                        <div class="modal-dialog modal-lg">
                          <div class="modal-content">
                            <!-- Modal Header -->
                            <div class="modal-header">
                              <h4 class="modal-title">
                                <svg
                                  width="24"
                                  height="24"
                                  viewBox="0 0 49 49"
                                  fill="none"
                                  xmlns="http://www.w3.org/2000/svg"
                                >
                                  <path
                                    d="M24.5 49C38.031 49 49 38.031 49 24.5C49 10.969 38.031 0 24.5 0C10.969 0 0 10.969 0 24.5C0 38.031 10.969 49 24.5 49Z"
                                    fill="#D7D0F6"
                                  />
                                  <path
                                    d="M24 36V24"
                                    stroke="white"
                                    stroke-width="6"
                                    stroke-linecap="round"
                                    stroke-linejoin="round"
                                  />
                                  <path
                                    d="M24 12H24.0292"
                                    stroke="white"
                                    stroke-width="6"
                                    stroke-linecap="round"
                                    stroke-linejoin="round"
                                  />
                                </svg>
                                Detail Perwalian Semester {{ riper.semester }}
                              </h4>
                              <button
                                type="button"
                                class="close"
                                data-dismiss="modal"
                              >
                                &times;
                              </button>
                            </div>

                            <!-- Modal body -->
                            <div class="modal-body">
                              <div class="row">
                                <div class="col-md-12">
                                  <div class="text-left text-dark">
                                    <strong
                                      >info : Pastikan Dosen Wali dan Bagian
                                      Keuangan telah menyetujui perwalian jika
                                      ingin muncul di absensi</strong
                                    >
                                  </div>
                                  <br />
                                  <div class="table-striped table-responsive">
                                    <table class="table mb-0 text-center">
                                      <thead>
                                        <tr>
                                          <th scope="col">No</th>
                                          <th scope="col">Nama MK</th>
                                          <th scope="col">Nama Kelas</th>
                                          <th scope="col">Dosen Wali</th>
                                          <th scope="col">Bag. Keuangan</th>
                                          <th scope="col">Catatan</th>
                                        </tr>
                                      </thead>
                                      <tbody>
                                        <tr
                                          v-for="(
                                            riperdet, index
                                          ) in getDataDetails"
                                          :key="riperdet.name_ina"
                                        >
                                          <td>{{ number(index) }}</td>
                                          <td class="text-left">
                                            {{ riperdet.name_ina }}
                                          </td>
                                          <td>
                                            {{ riperdet.class_name }}
                                          </td>
                                          <td>
                                           
                                            <span
                                              class="
                                                badge badge-warning badge-lg
                                              "
                                              v-if="
                                                riperdet.guardianship_approved ==
                                                0
                                              "
                                              >Menunggu</span
                                            >
                                            <span
                                              class="
                                                badge badge-success badge-lg
                                              "
                                              v-if="
                                                riperdet.guardianship_approved ==
                                                1
                                              "
                                              >Diterima</span
                                            >
                                          </td>
                                          <td>
                                            <span
                                              class="
                                                badge badge-warning badge-lg
                                              "
                                              v-if="
                                                riperdet.payment_approved ==
                                                0
                                              "
                                              >Menunggu</span
                                            >
                                            <span
                                              class="
                                                badge badge-success badge-lg
                                              "
                                              v-if="
                                                riperdet.payment_approved ==
                                                1
                                              "
                                              >Diterima</span
                                            >
                                          </td>
                                          <td>
                                            <span v-if="riperdet.noted == null"> - </span>
                                            <span v-else>{{riperdet.noted}}</span>
                                          </td>
                                        </tr>
                                      </tbody>
                                    </table>
                                  </div>
                                </div>
                              </div>
                            </div>

                            <!-- Modal footer -->
                            <div class="modal-footer">
                              <button
                                type="button"
                                class="btn btn-outline-secondary"
                                data-dismiss="modal"
                              >
                                Tutup
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                      <!-- batas modal -->
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "PerwalianHistory",
  data() {
    return {
      riper: [],
      riperdet: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataRiper: "riwayat/riwayat",
      getDataDetails: "riwayatdetail/detail_riwayat",
    }),
  },
  methods: {
    ...mapActions({
      riwayatper: "riwayat/riwayat",
      detailper: "riwayatdetail/detail",
    }),
    number: function (index) {
      return index + 1;
    },
    getHistoryAll() {
      this.riwayatper();
    },
    async getDetailAll(guardianship_id) {
      this.detailper(guardianship_id);
      // console.log(guardianship_id);
    },
  },
  mounted() {
    this.getHistoryAll();
    $("title").html("Riwayat Perwalian | STTBANDUNG");
  },
  components: {},
};
</script>

<style>
.check {
  color: green;
}

.times {
  color: red;
}

.keu {
  color: orange;
  background-color: rgb(253, 229, 180);
}

.acc {
  color: rgba(17, 119, 187, 0.993);
  background-color: rgb(180, 224, 253);
}

.tolak {
  color: brown;
  background-color: rgb(240, 172, 172);
}
</style>